<template>
  <div class="calogin">
    <div class="lineGradient">
      <div class="login-headbg" />
    </div>
    <div class="login-main negative">
      <div class="login-logo" />
      <van-tabs v-model="active" class="login-tab" animated swipeable>
        <van-tab title="用户登录">
          <div class="login-form">
            <van-cell-group>
              <div class="input-item">
                <van-field
                  ref="name"
                  v-model="users.userAccount"
                  v-validate="{'required': 'true', 'minChar': '2'}"
                  :error-message="errors.first('userAccount')"
                  clearable
                  name="userAccount"
                  placeholder="请输入统一社会信用代码"
                />
              </div>
              <div class="input-item password">
                <van-field
                  ref="id"
                  v-model="users.passWord"
                  v-validate="{'required': 'true', 'minChar': '1'}"
                  clearable
                  :error-message="errors.first('password')"
                  :type="eyesChange1 === true ?'text':'password'"
                  name="password"
                  placeholder="请输入登录密码"
                />
                <!-- 显示与隐藏密码 -->
                <div v-if="eyesChange1 ==true" class="input-operator showpassword" @click="showEye1()">
                  <i class="iconfont iconkejian" />
                </div>
                <div v-else class="input-operator showpassword" @click="showEye1()">
                  <i class="iconfont iconbukejian" />
                </div>
              </div>
            </van-cell-group>
            <div class="clear" />
            <!-- 登录按钮 begin -->

            <div class="login-button">
              <van-button round type="info" @click="gogyshome()">登 录</van-button>
            </div>
            <div class="login-button">
              <van-button round type="info" @click="goUrl()">URL</van-button>
            </div>
          </div>
        </van-tab>

        <van-tab title="移动证书登录" @click="getCAList()">
          <!--  移动证书登录 begin -->
          <div class="login-form">
            <van-cell-group>
              <van-cell :title="checkTitle" is-link arrow-direction="down" @click="showGysPopup" />
              <div class="input-item">
                <van-field
                  ref="id"
                  v-model="users.caPassWord"
                  clearable
                  is-highlight
                  is-title-latent
                  maxlength="6"
                  type="number"
                  placeholder="请输入移动证书PIN码"
                  name="PIN"
                />
                <p class="input-item-msg">{{ errors.first('PIN') }}</p>
              </div>
            </van-cell-group>

            <div class="login-button">
              <van-button round type="info" @click="caLoginAction()">CA登录</van-button>
            </div>
            <div class="login-button">
              <van-button round type="info" plain @click="goCaZhmm()">找回密码</van-button>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <!-- 选择供应商弹出层 beign -->
      <van-popup
        v-model="showPOP"
        position="bottom"
        :style="{ height: '50%' }"
      >
        <van-picker
          title="选择移动证书"
          show-toolbar
          :columns="columns"
          @confirm="onConfirm"
          @cancel="onCancel"
        />
      </van-popup>
    <!-- 选择供应商弹出层 end -->
    </div>
  </div>
</template>

<script>
// import headerBar from '@/components/header/headerBar'
// import popup from '@/components/popup/popup'
import { pageRouter, NativeJS, NativeCaJS } from '@/common/js/mixin.js'
import { getUserName, getPass } from '@/common/js/catch'
// import { getCert, getXLH, getPIN } from '@/common/js/catch'
import { loadSearch } from '@/common/js/catch'
import { mhLogin } from '@/store/module.js'
import { setToken, setUserName, setPass } from '@/common/js/catch'

export default {
  name: 'CaLogin',
  components: {
    // headerBar
  },
  mixins: [NativeCaJS, NativeJS, pageRouter],
  data() {
    return {
      active: 0,
      headerData: {
        title: '登录',
        leftUrl: 'back',
        leftText: ' '
      },
      checkTitle: '请选择用户证书', // 标题

      eyesChange1: false, // 是否展示密码
      eyesChange2: false, // 是否展示密码
      users: {
        userAccount: getUserName(),
        passWord: getPass(),
        caPassWord: getPass(),
        Pin: ''
      },
      showPOP: false, // 弹框
      imageList: {
        img1: require('@/assets/images/login/icon-ca.png')
      },
      columns: [],
      caListItem: [], // ca集合
      caListPin: [] // 密碼集合
    }
  },

  created() {
    this.users.userAccount = getUserName()
    this.users.passWord = getPass()

    if (this.users.userAccount != null && this.users.passWord != null) {
      this.isEmpty = false
    }
    var self = this
    // 二维码返回数据
    window['LoginSuccess'] = function(data) {
      console.info('LoginSuccess======', data)
      setToken(data.token) // 保存token
      setUserName(self.users.userAccount) // 保存登录名称
      setPass(self.users.passWord) // 保存密码
      self.$store.commit('mh/SET_MHUSER_INFO', data)
      self.$store.commit('mh/SET_IS_MHLOGIN', true)
      self.$toast.success('登录成功！')
      self.$router.push({ name: 'MhHome' })
    }
  },
  methods: {
    goUrl() {
      this.$router.push('MyUrl')
    },
    onConfirm(pick) {
      this.showPOP = false
      this.$store.commit('ca/SET_CaNUM', pick.value)
      const ca = loadSearch('caListItem')[pick.value]
      const ca1 = JSON.parse(ca)
      this.$store.commit('ca/SET_CaListItem', ca1)
      this.$store.commit('ca/SET_CaPin', loadSearch('caListPin')[pick.value])
      this.checkTitle = pick.text
    },
    onCancel() {
      this.showPOP = false
    },
    /* 控制是否展示密码*/
    showEye1() {
      this.eyesChange1 = !this.eyesChange1
    },
    showEye2() {
      this.eyesChange2 = !this.eyesChange2
    },

    showGysPopup() {
      this.showPOP = !this.showPOP
    },
    gogyshome() {
      console.log('gogyshome--------------------------------------------------------', this.active)
      this.$validator.validate().then(result => {
        if (result) {
          mhLogin.data = {
            username: this.users.userAccount,
            password: this.users.passWord
          }

          // if (process.env.NODE_ENV !== 'development') {
          //   try {
          //     var backData = window.myapp.loginURLAction(JSON.stringify({
          //       username: this.users.userAccount,
          //       password: this.users.passWord
          //     }))
          //     console.info('backData------', backData)
          //   } catch (err) {
          //     console.log(err)
          //   }
          this.$store.dispatch('mh/loginMh', {
            username: this.users.userAccount,
            password: this.users.passWord
          }).then(res => {
            this.NativeLoginAction(res.id)
            this.$toast.success('登录成功！')
            this.$router.push({ name: 'MhHome' })
          }).catch((error) => {
            this.$toast(error)
          })
          // } else {
          //   // this.$toast('返回原生页面')
          //   this.$store.dispatch('mh/loginMh', {
          //     username: this.users.userAccount,
          //     password: this.users.passWord
          //   }).then(res => {
          //     this.NativeLoginAction(res.id)
          //     this.$toast.success('登录成功！')
          //     this.$router.push({ name: 'MhHome' })
          //   }).catch((error) => {
          //     this.$toast(error)
          //   })
          // }
        }
      })
    },
    goCaxz() {
      this.$router.push({
        name: 'CaXz'
      })
    },
    goCaZhmm() {
      this.$router.push({
        name: 'CaZhmm'
      })
    },
    getHashData() {
      const ca = loadSearch('caListItem')[this.$store.state.ca.caNUM]
      const ca1 = JSON.parse(ca)
      console.log('ca1==11===', ca1.serialNo)
      const pins = loadSearch('caListPin')[this.$store.state.ca.caNUM]
      const pin = { 'CAXLH': ca1.serialNo,
        'newPin': pins,
        'singdata': '信源'
      }
      if (process.env.NODE_ENV !== 'development') {
        try {
          return window.myapp.signHashData(JSON.stringify(pin))
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('envelopeEncrypt', 'data', (data) => {
          return data
        })
      } else {
        this.$toast('返回原生页面')
      }
    },

    caLoginAction() {
      const ca = loadSearch('caListItem')[this.$store.state.ca.caNUM]
      const ca1 = JSON.parse(ca)
      console.log('ca1==11===', ca1.serialNo)
      this.$store.dispatch('ca/CaLoginAction', {
        ca_dn: ca1.dn,
        ca_signcert: ca1.signCert,
        ca_signdata: this.getHashData(), // 密文
        ca_data: '测试登录', // 明文
        ca_keymark: 'cfca'
      }).then(res => {
        // this.$store.dispatch('ca/setCaListItem', this.Native_CaList[this.checkNum])
        // this.$store.dispatch('ca/setCaPin', this.users.Pin)
        // this.NativeLoginAction(res)
        // this.$toast.success('登录成功！')
        this.$router.push({ name: 'MhHome' })
      }).catch((error) => {
        this.$toast(error)
      })
    }
  }
}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
